"use client"

import { useEffect, useState } from "react"

export default function Countdown({ targetDateISO }) {
  const target = new Date(targetDateISO).getTime()
  const [now, setNow] = useState(Date.now())

  useEffect(() => {
    const interval = setInterval(() => setNow(Date.now()), 1000)
    return () => clearInterval(interval)
  }, [])

  const diff = Math.max(0, target - now)
  const days = Math.floor(diff / (1000 * 60 * 60 * 24))
  const hours = Math.floor((diff / (1000 * 60 * 60)) % 24)
  const minutes = Math.floor((diff / (1000 * 60)) % 60)
  const seconds = Math.floor((diff / 1000) % 60)

  return (
    <div className="
      grid grid-cols-2 md:grid-cols-4 grid-rows-2 
      gap-4 w-full max-w-md mx-auto 
      auto-rows-fr
    ">
      
      {/* Bento maior ‚Äî Dias */}
      <div className="
        col-span-2 row-span-2 
        bg-white/80 backdrop-blur-xl rounded-3xl p-6 shadow-lg
        flex flex-col justify-center items-center
        border border-blue-100
        animate-[fadeIn_1s_ease]
      ">
        <div className="text-6xl md:text-7xl font-bold text-blue-900 drop-shadow">
          {String(days).padStart(2, "0")}
        </div>
        <div className="text-blue-700 mt-2 tracking-wide font-medium text-lg">
          Dias
        </div>
      </div>

      {/* Bento: Horas */}
      <BentoItem label="Horas" value={hours} />

      {/* Bento: Minutos */}
      <BentoItem label="Min" value={minutes} />

      {/* Bento: Segundos */}
      <BentoItem label="Seg" value={seconds} />

    </div>
  )
}

function BentoItem({ label, value }) {
  return (
    <div className="
      bg-white/70 backdrop-blur-lg rounded-2xl 
      p-4 shadow-md border border-blue-100
      flex flex-col justify-center items-center 
      animate-[fadeIn_1s_ease]
    ">
      <div className="text-3xl font-semibold text-blue-900">
        {String(value).padStart(2, "0")}
      </div>
      <div className="text-xs text-blue-700 mt-1 tracking-wide uppercase">
        {label}
      </div>
    </div>
  )
}
